import { Link } from "react-router-dom";
import logo from "./logo.png"
import './Navigation.css';

const Navigation: React.FC = () => {
    return (
      <nav className="main-nav">
        <Link to="/danbee"><img src={logo} alt="logo" className='logo' /></Link>
        <ul>
          <Link to="/danbee/main#intro"><li> </li></Link>
          <Link to="/danbee/main#fields"><li> </li></Link>
          <Link to="/danbee/main#program"><li></li></Link>
          <Link to="/danbee/main#process"><li> </li></Link>
          <Link to="/danbee/main#story"><li> </li></Link>
          <Link to="/danbee/diagnosis"><li></li></Link>
        </ul>
      </nav>
    );
  };

  export default Navigation;