import './Process.css'
import Footer from "../common/Footer";
import Navigation from "../common/Navigation";
import SequentialBlocks, { Block } from "./SequentialBlocks";

const Process: React.FC = () => {
    const processBlock: Block[] = [];
    processBlock.push({id: 1, content: "\n \n "});
    processBlock.push({id: 2, content: " \n 10 "});
    processBlock.push({id: 3, content: "\n \n "});
    processBlock.push({id: 4, content: "\n  "});
    processBlock.push({id: 5, content: "\n"});
    processBlock.push({id: 6, content: " \n"});

    return (
        <section id="process" className='process-body'>
            <h2></h2>
            <SequentialBlocks blocks={processBlock}></SequentialBlocks>
            <h3> </h3>
            <tbody>
                <tr>
                    <th>{""}</th>
                    <td>{" 10 ~  7 \n ( 2   .  9)"}</td>
                </tr>
                <tr>
                    <th>{""}</th>
                    <td>{" 10 ~  5"}</td>
                </tr>
                <tr>
                    <th>{""}</th>
                    <td>{""}</td>
                </tr>
            </tbody>
            <h3> </h3>
            <p> : 031-714-1714</p>
            <p> : </p>
            <p> : #</p>
            <h4> </h4>
            <p>  (ZOOM) </p>
            <p></p>
        </section>
)
};

export default Process;