import map from "../pic/map.PNG";
import './MapSection.css';

const MapSection: React.FC = () => {
    return (
      <section id="visit" className="map-section">
        <div className="map">
          <img src={map}/>
        </div>
        <div className="map-info">
          <h2> </h2>
          <p>    89</p>
          <h2></h2>
          <p>: abcde : (031) 714-1714</p>
          <h2></h2>
          <p> :  10 ~  7<br/>( 2   .  9)</p>
          <p> :  10 ~  5</p>
        </div>
      </section>
    );
  };

export default MapSection;